<script>
export default {
    name: 'HeaderComponent',
    data() {
        return {
            isBurgerOpen: false,
        };
    },
    methods: {
        toggleBurger() {
            this.isBurgerOpen = !this.isBurgerOpen;
            if (this.isBurgerOpen) {
                document.body.style.overflow = 'hidden';
            } else {
                document.body.style.overflow = 'auto';
            }
        },
        closeBurger() {
            this.isBurgerOpen = false;
            document.body.style.overflow = 'auto';
        },
    },
};
